import React from 'react';
import './Competences.scss';


import data from './data'


const Competences = () => {
  console.log(data);
  return (
    <div className="competences">
      <h2 className="competences-t">Compétences</h2>
      <div className="competences-u">
        {
          data.map(({ title, text, svg }, id) => (
            <div key={id} className="competences-container--width">
              <div className="competences-container">
                <h2 className="competences-title">{title}</h2>
                <p className="competences-text">{text}</p>
              </div>
              <hr  className="competences-hr"/>
            </div>
          ))
        }
      </div>
      <div className="notion">
        <h2 className="notion-title">Notions</h2>
        <ul className="notion-list">
          <li className="notion-list-item">PHP</li>
          <li className="notion-list-item">Lumen</li>
          <li className="notion-list-item">VueJS</li>
        </ul>
      </div>
    </div>
  )
}

export default Competences;